import React from "react";
import { useTranslation } from "react-i18next";
import {
  FaAngleDoubleDown,
  FaCog,
  FaEdit,
  FaSort,
  FaSortDown,
  FaSortUp,
} from "react-icons/fa";
import { Badge, Button, Col, Input, Row, Table } from "reactstrap";
import * as lodash from "lodash";

import "./TableBody.css";
import { formatTimestamp } from "utils/utils";

export default function TableBody({
  titles,
  detailTitles,
  items,
  currentSorter,
  handleCheckItemOnChange,
  handleCheckAllOnChange,
  handleSortOnClick,
  handleEditOnClick,
  handleShowDetailOnClick,
}) {
  const { t } = useTranslation();

  return (
    <div className="table-body">
      <Table responsive bordered>
        <thead>
          <tr>
            <th className="align-middle">
              <Input
                type="checkbox"
                className="align-checkbox"
                id="check-all"
                onChange={handleCheckAllOnChange}
              />
            </th>
            {titles &&
              titles.map((title, index) => (
                <th data-value={title.name} key={index}>
                  {title.icon}&nbsp;{title.text}
                  <div
                    className="pull-right"
                    onClick={() => handleSortOnClick(title.name)}
                  >
                    {title.name && currentSorter.sortBy !== title.name && (
                      <FaSort />
                    )}
                    {title.name &&
                      currentSorter.sortBy === title.name &&
                      currentSorter.sortType === "ASC" && <FaSortUp />}
                    {title.name &&
                      currentSorter.sortBy === title.name &&
                      currentSorter.sortType === "DESC" && <FaSortDown />}
                  </div>
                </th>
              ))}
            <th data-value="options">
              <FaCog /> {t("admin.common.OPTIONS")}
            </th>
          </tr>
        </thead>

        <tbody>
          {items &&
            items.map((item) => {
              return (
                <>
                  <tr
                    key={`row-${item.id}`}
                    className="row-table"
                    id={`row-${item.id}`}
                  >
                    <td className="align-middle">
                      <Input
                        type="checkbox"
                        className="align-checkbox"
                        name="row-checkbox"
                        id={`checkbox-${item.id}`}
                        value={item.id}
                        onChange={() => handleCheckItemOnChange(item.id)}
                      />
                    </td>
                    {titles &&
                      titles.map((title, index) => {
                        if (title.field === "isDeleted") {
                          return (
                            <td key={index}>
                              {item.isDeleted ? (
                                <Badge color="dark">
                                  {t("admin.common.DELETED")}
                                </Badge>
                              ) : (
                                <Badge color="light">
                                  {t("admin.common.AVAILABLE")}
                                </Badge>
                              )}
                            </td>
                          );
                        }
                        if (title.field === "chuanDauRaMonHoc") {
                          return (
                            <td key={index}>
                              {lodash.get(item, title.field) &&
                                lodash
                                  .get(item, title.field)
                                  .map((subjectOutputStandard, index) => {
                                    return (
                                      <div key={index}>
                                        {subjectOutputStandard.ma}
                                      </div>
                                    );
                                  })}
                            </td>
                          );
                        }
                        if (title.field === "syllabus") {
                          return (
                            <td key={index}>
                              {lodash.get(item, title.field) &&
                                `${lodash.get(item, title.field).monHoc.ma} - ${
                                  lodash.get(item, title.field).heDaoTao.ten
                                } - ${
                                  lodash.get(item, title.field).namHoc.ten
                                }`}
                            </td>
                          );
                        }
                        return (
                          <td key={index}>{lodash.get(item, title.field)}</td>
                        );
                      })}
                    <td>
                      <Button
                        color="light"
                        title={t("admin.common.EDIT")}
                        onClick={() => handleEditOnClick(item.id)}
                      >
                        <FaEdit fill="blue" />
                      </Button>
                      &nbsp;
                      <Button
                        color="light"
                        title={t("admin.common.SHOW_DETAIL")}
                        id={`show-detail-${item.id}`}
                        onClick={() => handleShowDetailOnClick(item.id)}
                      >
                        <FaAngleDoubleDown fill="green" />
                      </Button>
                    </td>
                  </tr>

                  <tr
                    key={`detail-row-${item.id}`}
                    className="detail-row d-none"
                    id={`detail-row-${item.id}`}
                  >
                    <td colSpan={1000}>
                      <Row className="table-detail">
                        <Col lg={12}>
                          {detailTitles &&
                            detailTitles.map((detailTitle, index) => {
                              if (detailTitle.field === "isDeleted") {
                                return (
                                  <Row key={index}>
                                    <Col lg={4} className="info-name">
                                      {t("admin.common.AVAILABLE_STATUS")}
                                    </Col>
                                    <Col lg={8} className="info-value">
                                      <span>
                                        {item.isDeleted ? (
                                          <Badge color="dark">
                                            {t("admin.common.DELETED")}
                                          </Badge>
                                        ) : (
                                          <Badge color="light">
                                            {t("admin.common.AVAILABLE")}
                                          </Badge>
                                        )}
                                      </span>
                                    </Col>
                                  </Row>
                                );
                              }
                              if (detailTitle.field === "syllabus") {
                                return (
                                  <Row key={index}>
                                    <Col lg={4} className="info-name">
                                      {t("Syllabus")}
                                    </Col>
                                    <Col lg={8} className="info-value">
                                      <span>
                                        {lodash.get(item, detailTitle.field) &&
                                          `${
                                            lodash.get(item, detailTitle.field)
                                              .monHoc.ma
                                          } - ${
                                            lodash.get(item, detailTitle.field)
                                              .heDaoTao.ten
                                          } - ${
                                            lodash.get(item, detailTitle.field)
                                              .namHoc.ten
                                          }`}
                                      </span>
                                    </Col>
                                  </Row>
                                );
                              }
                              if (detailTitle.field === "chuanDauRaMonHoc") {
                                return (
                                  <Row key={index}>
                                    <Col lg={4} className="info-name">
                                      {t(
                                        "admin.evaluation_activity_list.SUBJECT_OUTPUT_STANDARD"
                                      )}
                                    </Col>
                                    <Col lg={8} className="info-value">
                                      <span>
                                        {lodash.get(item, detailTitle.field) &&
                                          lodash
                                            .get(item, detailTitle.field)
                                            .map(
                                              (
                                                subjectOutputStandard,
                                                index
                                              ) => {
                                                return (
                                                  <div key={index}>
                                                    {subjectOutputStandard.ma}
                                                  </div>
                                                );
                                              }
                                            )}
                                      </span>
                                    </Col>
                                  </Row>
                                );
                              }
                              return (
                                <Row key={index}>
                                  <Col lg={4} className="info-name">
                                    {detailTitle.text}
                                  </Col>
                                  <Col lg={8} className="info-value">
                                    <span>
                                      {detailTitle?.isDate ? formatTimestamp(lodash.get(item, detailTitle.field)) : lodash.get(item, detailTitle.field)}
                                    </span>
                                  </Col>
                                </Row>
                              );
                            })}
                        </Col>
                      </Row>
                    </td>
                  </tr>
                </>
              );
            })}
        </tbody>
      </Table>
    </div>
  );
}
