import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { FaFilter } from "react-icons/fa";
import { toast } from "react-toastify";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";

import syllabusAPI from "api/common/syllabus";

export default function TableFilter({
  currentFilter,
  handleSyllabusFilterOnChange,
  handleFilterOnClick,
}) {
  const { t } = useTranslation();

  const [syllabuses, setSyllabuses] = useState([]);

  useEffect(async () => {
    try {
      const syllabusesRes = await syllabusAPI.getAll();
      setSyllabuses(syllabusesRes.contents);
    } catch (err) {// eslint-disable-next-line
    }
  }, []);

  return (
    <Form className="d-flex flex-wrap">
      <FormGroup>
        <Label for="syllabus">{t("Syllabus")}</Label>
        <Input
          type="select"
          value={currentFilter.syllabus !== null ? currentFilter.syllabus : -1}
          onChange={handleSyllabusFilterOnChange}
        >
          <option value={-1}>{t("admin.common.ALL")}</option>
          {syllabuses &&
            syllabuses.map((item, index) => {
              return (
                <option key={index} value={item.id}>
                  {`${item.monHoc.ma} - ${item.heDaoTao.ten} - ${item.namHoc.ten}`}
                </option>
              );
            })}
        </Input>
      </FormGroup>
      &emsp;
      <FormGroup className="align-self-end">
        <Button onClick={handleFilterOnClick}>
          <FaFilter /> {t("admin.common.FILTER")}
        </Button>
      </FormGroup>
    </Form>
  );
}
